<template>
    <div></div>
</template>
<script>
export default {
    props: {
        // 
        inputText: {
            type: String,
            default: "waterMark",
        },
        // jsDOMidattribute
        // true
        inputAllowDele: {
            type: Boolean,
            default: false,
        },
        // DOM
        // true
        inputDestroy: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            maskDiv: {}, // divDOM
        };
    },
    mounted() {
        // DOM
        this.$nextTick(() => {
            // 
            this.init();
            if (!this.inputAllowDele) {
                // DOM
                this.Monitor();
            }
        });
    },
    methods: {
        init() {
            let canvas = document.createElement("canvas");
            canvas.id = "canvas";
            canvas.width = "200"; // 
            canvas.height = "130";
            this.maskDiv = document.createElement("div");
            let ctx = canvas.getContext("2d");
            ctx.font = "normal 18px Microsoft Yahei"; // 
            ctx.fillStyle = "rgba(112, 113, 114, 0.1)"; // 
            ctx.rotate((30 * Math.PI) / 180); // 
            ctx.fillText(this.inputText, 30, 20);
            let src = canvas.toDataURL("image/png");
            this.maskDiv.style.position = "fixed";
            this.maskDiv.style.zIndex = "9999";
            this.maskDiv.id = "_waterMark";
            this.maskDiv.style.top = "30px";
            this.maskDiv.style.left = "0";
            this.maskDiv.style.height = "100%";
            this.maskDiv.style.width = "100%";
            this.maskDiv.style.pointerEvents = "none";
            this.maskDiv.style.backgroundImage = "URL(" + src + ")";
            // body
            document.body.appendChild(this.maskDiv);
        },
        Monitor() {
            let body = document.getElementsByTagName("body")[0];
            let options = {
                childList: true,
                attributes: true,
                characterData: true,
                subtree: true,
                attributeOldValue: true,
                characterDataOldValue: true,
            };
            let observer = new MutationObserver(this.callback);
            observer.observe(body, options); // body
        },
        // DOMcallback
        callback(mutations, observer) {
            // attribute
            if (mutations[0].target.id === "_waterMark") {
                this.removeMaskDiv();
            }
            // id
            if (mutations[0].attributeName === "id") {
                this.removeMaskDiv();
                this.init();
            }
            // 
            if (
                mutations[0].removedNodes[0] &&
                mutations[0].removedNodes[0].id === "_waterMark"
            ) {
                this.init();
            }
        },
        /* public */
        // DOM
        removeMaskDiv() {
            document.body.removeChild(this.maskDiv);
        },
        // 
        createMaskDiv() {
            this.init();
        },
    },
    watch: {
        // 
        inputText() {
            this.$nextTick(() => {
                this.removeMaskDiv();
            });
        },
    },
    destroy() {
        // body
        if (this.inputDestroy) {
            this.removeMaskDiv();
        }
    },
};
</script>