<template>
  <div id="3d-map" class="w-full h-full">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    mapName: {
      type: String,
      default: "",
    },
    mapData: {
      type: Object,
      default: () => {},
    },
    el: {
      type: String,
      default: "3d-map",
    },
    isDrill: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      chart: null,
      timeTicket: null,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.chart = this.$echarts.init(document.getElementById(this.el));
      this.$echarts.registerMap(this.mapName, this.mapData);
      let options = {
        geo: {
          map: this.mapName, //
          zoom: 1,
          roam: true,
          animation: false,
          itemStyle: {
            // 
            areaColor: {
              type: "radial",
              x: 0.5,
              y: 0.5,
              r: 0.8,
              colorStops: [
                {
                  offset: 0,
                  color: "rgba(147, 235, 248, 1)", // 0% 
                },
                {
                  offset: 1,
                  color: "rgba(2, 99, 206, 1)", // 100% 
                },
              ],
              globalCoord: false, //  false
            },
            shadowColor: "#105781", //
            shadowOffsetX: 0,
            shadowOffsetY: 10,
          },
        },
        series: [
          {
            name: "map",
            type: "map", // 
            map: this.mapName, // 
            selectedMode: false, // 
            roam: true, // scalemove
            // 
            label: {
              show: true,
              color: "#000a3c",
            },
            // 
            itemStyle: {
              // 
              areaColor: {
                type: "radial",
                x: 0.5,
                y: 0.5,
                r: 3,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(223, 231, 242, 1)", // 0% 
                  },
                  {
                    offset: 1,
                    color: "rgba(2, 99, 206, 1)", // 100% 
                  },
                ],
                globalCoord: false, //  false
              },
              borderWidth: 1, // 
              borderColor: "rgba(104, 152, 190, 1)", // 
              shadowColor: "rgba(128, 217, 248, 1)", // 
              shadowOffsetX: -2, // 
              shadowOffsetY: 2, // 
              shadowBlur: 10, // 
            },
            // 
            emphasis: {
              label: {
                color: "#ffffff",
              },
              itemStyle: {
                areaColor: "#a5d4fe",
              },
            },
          },
        ],
      };
      this.chart.setOption(options);
      this.chart.on("georoam", (params) => {
        let option = this.chart.getOption(); //option
        if (params.zoom != null && params.zoom != undefined) {
          //
          option.geo[0].zoom = option.series[0].zoom; //geogeo
          option.geo[0].center = option.series[0].center; //geogeo
        } else {
          //
          option.geo[0].center = option.series[0].center; //geogeo
        }
        this.chart.setOption(option); //option
      });
      this.chart.off("click");
      this.chart.off("dblclick");
      this.chart.on("click", (params) => {
        if (!this.isDrill) return;
        this.timeTicket = setTimeout(() => {
          // 
          this.$emit("down", params.name);
        }, 300);
      });
      this.chart.on("dblclick", (params) => {
        if (!this.isDrill) return;
        clearTimeout(this.timeTicket);
        this.$emit("up");
      });

      window.addEventListener("resize", () => {
        this.chart && this.chart.resize();
      });
    },
  },
};
</script>

